export default {
	data() {
		return {
			storeList: [],
			longitude: 0,
			latitude: 0,
			covers: [{
				'longitude': 0,
				'latitude': 0
			}]
		};
	},
	onLoad() {
		this.getMyLocation();
		this.getStoreList();
	},
	methods: {
		// 获取自己的位置信息
		getMyLocation() {
			let _this = this;
			uni.getLocation({
			    type: 'wgs84',
			    success: function (res) {
					_this.longitude = res.longitude;
					_this.latitude = res.latitude;
			    },
				fail: function () {
					_this.$common.errorToShow("获取位置信息失败")
				}
			});

		},
		// 获取店铺列表信息
		getStoreList() {
			let _this = this;
			_this.$api.storeList({}, res => {
				if (res.status) {
					_this.storeList = res.data;
					let storeList = res.data;
					let covers = [];
					for (let i=0; i < storeList.length; i++) {
						let newArr = {}
						newArr.latitude = storeList[i].latitude;
						newArr.longitude = storeList[i].longitude;
						newArr.width = '50rpx';
						newArr.height = '50rpx';
						newArr.iconPath = '/static/image/gps-blue.png'
						covers.push(newArr)
						
					}
					//console.log(covers)
					_this.covers = covers;
				} else {
				}
				// console.log(res)
			});
		}
	}
};
